
.resume_container{
  
        grid-template-columns: 20fr 5fr;
        column-gap: 1.877rem;
        
}
.timeline{
    background-color: var(--container-color);
    padding:1.875rem;
    border-radius: var(--border-radius);
    position: relative;
    box-shadow: var(--shadow);
    
    
}
.timeline::before{
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 15px solid var(--container-color);
    position: absolute;
    right: -0.83rem;
    top:15%;
    content: '';
    width:0;
    height:0;
}

.timeline_item{
    position: relative;
    padding-left: 3.125rem;
    padding-bottom: 3.125rem;
}
.timeline_item:last-child{
    padding-bottom: 0;
}
.timeline_item::before{
    content: '';
    width: 1px;
    height: 100%;
    background-color: var(--first-color);
    position: absolute;
    left: .25rem;
    top:0;
}

.timeline .icon-briefcase,
.timeline .icon-graduation{
    position: absolute;
    left: -0.4375rem;
    top:0;
    font-size: var(--h2-font-size);
    color: var(--first-color);
    background-color: var(--container-color);
    padding: 0.4375rem 0;
}

.timeline_date{
    color:#8b88b1;
    font-size: var(--small-font-size);
}
.timeline_title{
    font-size: var(--h3-font-size);
    margin:0.5rem 0;
}

.about_img1{
    margin-left:10px;
    margin-bottom: 40px;
    width: 200px;
    height: 200px;
}



  